//pages.Assignment.js

import {useState} from 'react';
import Layout from '../components/shared/Layout';
import styles from './Assignment.module.css';
function Assignment () {
  const [value, setValue] = useState(true);

  function onClick() {
    setValue(!value);
  }
  return (
      <Layout>
        <div>
            {value ? <div className={styles.true}>True입니다</div> : <div className={styles.false}>False입니다</div>}
            <button onClick={onClick}> 색 바꾸기</button>
        </div>
      </Layout>

  )
}

export default Assignment;