// src/components/shared/Layout.js

import styles from './Layout.module.css';
import Header from './Header';
import Menu from './Menu';
import {useState} from 'react';

function Layout({ children, activeMenu}) {
  const [showMenu, setValue] = useState(true);
  function onClick() {
    setValue(!showMenu);
    console.log(showMenu);
  }
  return (
    <div className={styles.container}>
      <Header onClick={onClick}/>
      <div className={styles.layout}>
          <Menu activeMenu={activeMenu} showMenu={showMenu}/>
          {showMenu ? <div className={styles.contents}>{children}</div>
          : <div className={styles.nopaddingContents}> {children} </div>}
      </div>
    </div>
  );
}

export default Layout;