import styles from './Header.module.css';
import youtube_logo from '../../data/youtube_logo.png';
import { FiMenu } from 'react-icons/fi';
import { IoSearchOutline } from 'react-icons/io5';
import { BsGrid, BsGrid3X3Gap } from 'react-icons/bs';
import { HiOutlineDotsVertical } from 'react-icons/hi';
import {useState} from 'react';
import { MdAirlineSeatIndividualSuite } from 'react-icons/md';

function Header({onClick}) {
  const [input, setInput] = useState('');
  
  function onInput() {
    console.log(input);
    setInput('');
  }
  function onChange(event){
    setInput(event.target.value);
  }

  return (
    <div className={styles.header}>
      <div className={styles.tab}>
        <FiMenu className={styles.icon} 
          onClick={onClick} />
        <img src={youtube_logo} alt="로고" className={styles.logo} />
      </div>
      <div className={styles['center-tab']}>
        <input onChange={onChange} value={input} className={styles.input} />
        <IoSearchOutline className={styles['search-icon']} onClick={onInput} />
      </div>
      <div className={styles.tab}>
        <BsGrid3X3Gap className={styles.icon} />
        <HiOutlineDotsVertical className={styles.icon} />
      </div>
    </div>
  );
}

export default Header;